.login_container {
	width: 100%;
	min-height: 100vh;
	background-color: #f1ede9;
    background-image: linear-gradient(74deg, #f1ede9 37%, #9d9ba1 100%);
	
	align-items: center;
	justify-content: center;
}

.login_form_container {
	width: 1200px;
	height: 800px;
    margin-top: 20px;
	display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
	border-radius: 10px;
	box-shadow: 0px 3px 3px -2px rgb(0 0 0 / 20%),
		0px 3px 4px 0px rgb(0 0 0 / 14%), 0px 1px 8px 0px rgb(0 0 0 / 12%);
}
.icon{
    width: 30px;
	height: 30px;
    color: rgb(160, 72, 0);
    
    padding-right: 15px;
}
.box{
    width: 800px;
	height: 100px;
}
.text{
	justify-content: center;
	padding-top: 5vh;
	margin-right: 5vh;
	margin-left: 5vh;
}
